const SELECTED_ANSWER = 'SELECTED_ANSWER'

export const selectedAnswerACTWO = (questionId, answerId) => ({ type: SELECTED_ANSWER, questionId, answerId })

let initialState = {
    questions: 
    [{id: `f${(~~(Math.random() * 1e8)).toString(16)}`, question: '  ,   .  ?',
        answer: [{ id: `f${(~~(Math.random() * 1e8)).toString(16)}`, possible: '', correct: true },
        { id: `f${(~~(Math.random() * 1e8)).toString(16)}`, possible: '', correct: false },
        { id: `f${(~~(Math.random() * 1e8)).toString(16)}`, possible: '', correct: false },
        { id: `f${(~~(Math.random() * 1e8)).toString(16)}`, possible: '', correct: false }], answerUser: ''},
    { id: `f${(~~(Math.random() * 1e8)).toString(16)}`, question: '  ,   .   ?', 
        answer: [{ id: `f${(~~(Math.random() * 1e8)).toString(16)}`, possible: '', correct: false },
        { id: `f${(~~(Math.random() * 1e8)).toString(16)}`, possible: '', correct: false },
        { id: `f${(~~(Math.random() * 1e8)).toString(16)}`, possible: '', correct: true },
        { id: `f${(~~(Math.random() * 1e8)).toString(16)}`, possible: '', correct: false }], answerUser: ''},
    { id: `f${(~~(Math.random() * 1e8)).toString(16)}`, question: ' ,  .   ,   .', 
        answer: [{ id: `f${(~~(Math.random() * 1e8)).toString(16)}`, possible: '', correct: false },
        { id: `f${(~~(Math.random() * 1e8)).toString(16)}`, possible: '', correct: false },
        { id: `f${(~~(Math.random() * 1e8)).toString(16)}`, possible: '', correct: false },
        { id: `f${(~~(Math.random() * 1e8)).toString(16)}`, possible: '', correct: true }], answerUser: ''},
    { id: `f${(~~(Math.random() * 1e8)).toString(16)}`, question: '  ,   ?', 
        answer: [{ id: `f${(~~(Math.random() * 1e8)).toString(16)}`, possible: 'C', correct: false },
        { id: `f${(~~(Math.random() * 1e8)).toString(16)}`, possible: '', correct: true },
        { id: `f${(~~(Math.random() * 1e8)).toString(16)}`, possible: '', correct: false },
        { id: `f${(~~(Math.random() * 1e8)).toString(16)}`, possible: '', correct: false }], answerUser: ''},
    { id: `f${(~~(Math.random() * 1e8)).toString(16)}`, question: '      ?', 
        answer: [{ id: `f${(~~(Math.random() * 1e8)).toString(16)}`, possible: '', correct: false },
        { id: `f${(~~(Math.random() * 1e8)).toString(16)}`, possible: '. ', correct: false },
        { id: `f${(~~(Math.random() * 1e8)).toString(16)}`, possible: '', correct: true },
        { id: `f${(~~(Math.random() * 1e8)).toString(16)}`, possible: '', correct: false }], answerUser: ''},
    { id: `f${(~~(Math.random() * 1e8)).toString(16)}`, question: '   ?', 
        answer: [{ id: `f${(~~(Math.random() * 1e8)).toString(16)}`, possible: '', correct: false },
        { id: `f${(~~(Math.random() * 1e8)).toString(16)}`, possible: '', correct: true },
        { id: `f${(~~(Math.random() * 1e8)).toString(16)}`, possible: '', correct: false },
        { id: `f${(~~(Math.random() * 1e8)).toString(16)}`, possible: '', correct: false }], answerUser: ''},
    { id: `f${(~~(Math.random() * 1e8)).toString(16)}`, question: '     ?', 
        answer: [{ id: `f${(~~(Math.random() * 1e8)).toString(16)}`, possible: '149 000 000 ', correct: true },
        { id: `f${(~~(Math.random() * 1e8)).toString(16)}`, possible: '14 900 ', correct: false },
        { id: `f${(~~(Math.random() * 1e8)).toString(16)}`, possible: '257 800 000 ', correct: false },
        { id: `f${(~~(Math.random() * 1e8)).toString(16)}`, possible: '190 000 000 ', correct: false }], answerUser: ''}],
    answersGiven: 0,
    correctAnswers: 0,
}

const testReducer = (state = initialState, action) => {
    let stateCopy;
    switch (action.type) {
        case SELECTED_ANSWER:
            let arr = state.questions.map(item => {
                if (item.id == action.questionId) {
                    item.answer.map(answerItem =>{
                        if (answerItem.id == action.answerId) {
                            item.answerUser = answerItem.possible
                            state.answersGiven++;
                            if (answerItem.correct) state.correctAnswers++
                        }
                    })
                    return item    
                }
                return item 
            })
            console.log(state.correctAnswers)
            stateCopy = {
                ...state,
                questions: [...arr],
            }
            return stateCopy
        default:
            return state

    }
}

export default testReducer